/* Minimal layout utilities for centering the React95 window */
html, body, #root {
  height: 100%;
}
body {
  margin: 0;
  overflow: hidden; /* lock page from scrolling */
}

.appRoot {
  height: 100vh;
  display: grid;
  place-items: center;
  padding: 0;
  overflow: hidden;
}

.windowWrap {
  --size: min(92vmin, 520px);
  width: var(--size);
  height: var(--size);
}

.stack {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.aboutWrap {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.row {
  display: flex;
  align-items: center;
  gap: 8px;
}

.iconsRow {
  display: flex;
  align-items: center;
  gap: 10px;
}

/* Music layout: centered art, controls below */
.musicCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.artArea {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-bottom: -10px;
}

.albumArtHuge {
  margin: 0;
  width: 100%;
  max-height: 100%;
  aspect-ratio: 1 / 1;
  height: auto;
  object-fit: cover;
  display: block;
  /* Slight retro pixelation for the album cover */
  image-rendering: pixelated;
}

/* Pixelation wrapper: renders image at lower res then scales up */
.pixelArt {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.pixelArt .albumArtHuge {
  /* Render the image smaller, then scale up with nearest-neighbor */
  width: calc(100% / var(--pixelate, 1));
  height: calc(100% / var(--pixelate, 1));
  transform: scale(var(--pixelate, 1));
  transform-origin: top left;
  image-rendering: pixelated;
  object-fit: cover;
}

.bottomArea {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: var(--music-gap, 8px);
  margin-top: var(--music-gap, 8px);
}

.controlsRow {
  display: flex;
  align-items: center;
  gap: 4px;
  width: 100%;
}

/* Centered vertical layout for controls + progress */
.controlsBlock {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
  width: 100%;
}

/* Progress + timer inline and centered */
.progressRow {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 18px;
}

.controlsLeft {
  display: inline-flex;
  align-items: center;
  gap: 2px;
}

.controlsRight {
  display: flex;
  align-items: center;
  gap: 4px;
  flex: 1;
}

.squareButton {
  width: 20px;
  height: 20px;
  min-width: 20px;
  padding: 0;
  display: inline-grid;
  place-items: center;
}

.icon24 {
  width: 20px;
  height: 20px;
  image-rendering: pixelated;
}

.icon24Svg {
  width: 14px;
  height: 14px;
  color: var(--icon-color, #000);
}

.icon24SvgLg {
  width: 20px;
  height: 20px;
  color: var(--icon-color, #000);
}

/* Masked PNG so we can color it via CSS */
.iconMask {
  display: inline-block;
  width: 14px;
  height: 14px;
  background-color: var(--icon-color, #000);
  margin-bottom: 2px;
}
.linkedinMask {
  -webkit-mask: url('./icons/linkedin.png') no-repeat center / contain;
  mask: url('./icons/linkedin.png') no-repeat center / contain;
}

.icon24Svg {
  width: 14px;
  height: 14px;
  color: var(--icon-color, #000);
}

.shrink80 {
  transform: scale(0.8);
  transform-origin: center;
}

.marqueeWrap {
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  padding: 4px 6px;
  max-width: 100%;
}

/* Constrain marquee to album art width */
.marqueeBox {
  width: 100%;
  max-width: 300px;
  margin: 0 auto;
}

.marqueeText {
  display: inline-block;
  will-change: transform;
  padding-right: 5px; /* trailing buffer so last glyph isn't clipped */
}

@keyframes marqueeSlide {
  0% { transform: translateX(0); }
  10% { transform: translateX(0); }
  90% { transform: translateX(calc(-1 * var(--scroll-dist, 0px))); }
  100% { transform: translateX(calc(-1 * var(--scroll-dist, 0px))); }
}

.marqueeText.running {
  animation: marqueeSlide var(--scroll-time, 6s) linear infinite;
}

/* Basic styling for markdown-rendered blog posts */
.markdownBody h1, .markdownBody h2, .markdownBody h3,
.markdownBody h4, .markdownBody h5, .markdownBody h6 {
  margin: 0.5rem 0 0.25rem;
}
.markdownBody p {
  margin: 0.5rem 0;
}
.markdownBody a {
  color: inherit;
  text-decoration: underline;
}
.markdownBody img {
  max-width: 100%;
  height: auto;
  display: block;
  margin: 0.5rem auto; /* center horizontally */
}
.markdownBody code {
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  background: rgba(0,0,0,0.08);
  padding: 0 3px;
  border-radius: 2px;
}
/* Ensure emphasis renders clearly */
.markdownBody strong { font-weight: bold; }
.markdownBody em { font-style: italic; }

/* Center title/artist when not scrolling */
.marqueeWrap.centered {
  text-align: center;
}
.marqueeWrap.scrolling {
  text-align: left;
}

/* Custom progress (no percentage, no transitions) */
.progressOuter {
  width: 100%;
  height: 10px;
  background: var(--progress-bg, #ffffff);
  border: 1px solid var(--progress-border, #000);
  overflow: hidden;
}
.progressInner {
  width: 100%;
  height: 100%;
  background: var(--progress-fill, #2b58d4);
  transform-origin: left center;
  transform: scaleX(0);
  will-change: transform;
  transition: none;
}

.progressInner.noAnim {
  transition: none;
}

/* Helpers for layouting progress/time area */
.progressWrap { flex: 0 0 auto; width: var(--progress-w, 200px); margin: 0 auto; }
.timeText {
  position: absolute;
  left: 50%;
  transform: translateX(calc(var(--progress-w, 200px) / 2 + 8px));
  white-space: nowrap;
  font-size: 12px;
  line-height: 1;
  opacity: 0.9;
  margin: 0;
  /* Prevent jitter as numbers change */
  font-variant-numeric: tabular-nums;
  font-feature-settings: 'tnum' 1, 'lnum' 1;
}

.timeText .current {
  display: inline-block;
  width: 5ch; /* keeps "/ total" aligned; up to 99:59 */
  text-align: right;
}

/* Bulleted list styling (React95 reset removes defaults) */
.bulletList {
  list-style: disc;
  padding-left: 20px;
  margin: 0;
}
.bulletList li { margin: 2px 0; }

.spacer {
  height: 8px;
}
